.App {
  text-align: center;

}

body {
    overflow: hidden;
}

.title {
  position: absolute;
  
}

.background {
  width: 1920px;
  height: 1080px;
  background-color: #313131;
}

.ok-title {
  position: absolute;
  top: 100px;
  font-size: 150px;
  height: 150px;
}

.community-title {
  position: absolute;
  top: 300px;
  font-size: 100px;
  height: 100px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #181818;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
